.lapped-card{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 100%;

}
.lapped-card-wrap {
  background: #565f77 none repeat scroll 0 0;
  color: #000;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: absolute;

}
.lapped-card .center-wrap{
  width: 320px;
  height: 380px;
}
.lapped-card .center-wrap ul{
  width: 100%;
  height: 100%;
  position: relative;
  list-style: none;
  margin:0;
  padding: 0;
  /*子元素视距*/
  perspective: 1000px;
  /*透视位置*/
  perspective-origin: 50% 150%;

  -webkit-perspective: 1000px;
  -webkit-perspective-origin: 50% 150%;

}
.lapped-card .center-wrap ul>li{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;

  opacity: 1;

  -webkit-transition-timing-function: ease;

       -o-transition-timing-function: ease;

          transition-timing-function: ease;
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
  pointer-events: auto;
  -webkit-box-shadow: 1px 1px 6px #555;
          box-shadow: 1px 1px 6px #555;
  border-radius: 8px;
  overflow: hidden;

}
.lapped-card .center-wrap ul>li>div{
  width: 100%;
  height:320px;
  display: block;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
  overflow: hidden;

}
.lapped-card .center-wrap ul>li>section{
  -ms-flex: 1 1;
      flex: 1 1;
  background-color: #fff;
  /*border-bottom-left-radius: 8px;*/
  /*border-bottom-right-radius: 8px;*/

}
.lapped-card .center-wrap ul>li>section{
  padding: 10px 8px;
}
.lapped-card .center-wrap ul>li>section p{
  margin: 0;
}
.lapped-card  .card-name{
  color: #333;
}
.lapped-card .card-sign{
  font-size: 12px;
  color: #666;
}
.lapped-card .center-wrap ul>li>div img{
  pointer-events: none;
  display: block;

  width:320px;
}



